import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { TableData, ColEditDelete } from 'components/common';
import {
  getGraduateClassification,
  deleteGraduateClassification,
} from 'services/GraduateClassification';
import { Card, Row, Col, message } from 'antd';

class RatingGraduatePage extends Component {
  state = {};

  handleDelete = async id => {
    await deleteGraduateClassification(id);
    message.success('Xóa thành công');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Xếp hạng',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Từ điểm',
        dataIndex: 'fromGrade',
        key: 'fromGrade',
      },
      {
        title: 'Đến điểm',
        dataIndex: 'toGrade',
        key: 'toGrade',
      },
      {
        title: 'Từ điểm thang 10',
        dataIndex: 'fromGradeIn10',
        key: 'fromGradeIn10',
      },
      {
        title: 'Đến điểm thang 10',
        dataIndex: 'toGradeIn10',
        key: 'toGradeIn10',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#rating-graduate/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Danh mục xếp hạng tốt nghiệp">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/rating-graduate/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getGraduateClassification}
              columns={columns}
              rowKey={record => record.label + record.id}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RatingGraduatePage />
  </Page>
);



